body {
  margin: 20px;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  text-align: center;
}

.search-form {
  background-color: #87ceeb;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
}

.search-form input[type="text"] {
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  display: grid;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  margin-top: 10px;
}

.info-item {
  margin: 0;
}

.load-more {
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

